import React, { PureComponent } from "react";
import Image from "../../Elements/Media/Image";

export default class Media extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: props.itemData.postFileNew
    }
  }
  componentDidMount() {
    const { itemData } = this.props
    this.setState({ imgUrl: itemData.postFileNew })
  }
  onLoad = event => {
    if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
  }
  componentWillReceiveProps(nextProps) {
    const { itemData } = nextProps;
    this.setState({ imgUrl: itemData.postFileNew })
  }

  render() {
    const { itemData, wallID, ownerId, ThemeID } = this.props;
    const mediaType = (itemData.type === 3 || itemData.type === 5) ? true : false
    const isYoutubePost = (itemData.network.id === 7) ? true : false
    const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
    const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_cp_multi_images_ico_bottom' : 'tb_cp_multi_images_ico'
    const isAudio = itemData.isAudio === 1 ? true : false
    // const imageStyle = {
    //   paddingBottom: '100%', width: '100%'
    // };
    return <div className="tb_cp_media_wrap">
      {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}><div></div></div> : ''}
      {mediaType ? isYoutubePost ? <div className="tb_cp_youtube_ico tb__icon tb-youtube-default"><div className="tb_color_icon__ tb_d_youtube_path_1"><div></div></div><div className="tb_color_icon__ tb_d_youtube_path_2"><div></div></div></div> :
      isAudio ? <div className="tb_cp_audio_ico tb__icon tb-audio"><div></div></div> :
      <div className="tb_cp_video_ico tb__icon tb-video"><div></div></div> : ''}
      <Image ImageClass="tb_cp_image" data={itemData} wallID={wallID} ownerId={ownerId} size={100} />

    </div>
  }
}
